.dialogs {
    display: flex;
    flex-direction: column;
}
.pageBody {
    display: flex;
    flex-direction: column;
}
.imgDiv {
    text-align: center;
}
.img {
    flex-direction: row;
    margin-top: 2rem;
    max-height: 100px;
    background-color: black;
    max-width: fit-content;
    border-radius: 50px;
    margin-bottom: 3rem;
    box-shadow: rgba(18, 32, 37, 0.76) 2px 1px 3px 1px;
}
.dAndM {
    display: flex;
    flex-direction: row;

}
.dialog {
    padding-right: 3rem;
    justify-self: start;
    display: flex;
    flex-direction: column;
}
.dialogItem {
    margin: 0.5rem;
    color: dimgray;
    text-decoration: none;
}
.act {
    margin: 0.5rem;
    color: #232a32;
    text-decoration: none;
    font-weight: bold;
}

.sendButton {
    margin-right: 2rem;
    border-radius: 50px;
    box-shadow: rgba(18, 32, 37, 0.76) 2px 1px 3px 1px;
}


