.messageTextBody {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    justify-self: stretch;
    align-self: stretch;
    align-items: center;
    margin-bottom: 4rem;
}
.messageText {
    padding: 1rem;
    border-radius: 20px;
}

.button {
    margin-top: 1rem;
    border-radius: 15%;
    display: flex;
    align-content: center;
}